import react from 'react';
import {
    Navigate,
    useNavigate,
} from 'react-router-dom';
import IcoTilin from '../../images/TilinIco.png';

const NotFound = () => {
    const navigate = useNavigate();
    const NotFound = (e) => {
        navigate('/login');
    }
    
    return (
        <div class="h-screen w-screen  bg-gray-700 flex items-center">
            <div class="container flex flex-col md:flex-row items-center justify-center px-5 text-gray-100">
                <div class="max-w-md">
                    <div class="text-5xl font-dark font-bold">404</div>
                    <p
                    class="text-2xl md:text-3xl font-light leading-normal"
                    >Guauf Guauf. </p>
                <p class="mb-8">uwu</p>
                
                <button onClick={(e)=>NotFound(e)} class="px-4 inline py-2 text-sm font-medium leading-5 shadow text-white transition-colors duration-150 border border-transparent rounded-lg focus:outline-none focus:shadow-outline-blue bg-blue-600 active:bg-blue-600 hover:bg-blue-700">Back to Login</button>
            </div>
            <div class="max-w-lg">
                <img src={IcoTilin} class="w-full h-auto" />
            </div>
            
        </div>
        </div>
    );
}

export default NotFound;