const BASE_URL = "https://posts-pw2021.herokuapp.com/api/v1";

const services = {};

//----------------------------------------------------------------------------------------------------------------------
//POSTS

//Crear post (CREATE)
services.createPost = async (token,title,description,image) => {
    const response = await fetch(`${BASE_URL}/post/create`, {
        method: "POST",
        headers: {
            "Authorization": `Bearer ${token}`,
            "Content-type": "application/json"

        },
        body: JSON.stringify({
            title: title,
            description: description,
            image: image
        })
    });
}

//Post creados por el usuario que consulta(OWNED)
services.Ownedpost = async (token,offset) => {
    const response = await fetch(`${BASE_URL}/post/owned?limit=15&page=${offset}`, {
        method: "GET",
        headers: {
            "Authorization": `Bearer ${token}`,
        }
    });

    if (response.ok) {
        const data = await response.json();
        return data;
    }
    return {};
}

//Update Post(UPDATE)
services.updatePost = async (token,id,title,description,url) => {
    const response = await fetch(`${BASE_URL}/post/update/${id}`, {
        method: "PUT",
        headers: {
            "Content-type": "application/json",
            "Authorization": `Bearer ${token}`

        },
        body: JSON.stringify({
            title: title,
            description: description,
            image: url
        })
    });

    if (response.ok) {
        const data = await response.json();
        
        return data;
    }

    return {}

}

