import { useNavigate } from "react-router-dom";
import React, { useEffect, useState } from 'react';
import { useUserContext } from "../../contexts/UserContext";
import services from "../../services/user.services";
import TilinPost from '../../components/PostAdmin';
import IcoUser from '../../images/user.png';
import IcoTilin from '../../images/TilinIco.png'

const FavoritesUser = (props) => {
    const {token,user} = useUserContext();
    const auth = useUserContext();
    const navigate = useNavigate();
    const [favorites, setFavorites] = useState([]);


    const onClickHandler = (e) =>{
        e.preventDefault();

        auth.logout();
        navigate("/login");
    }

    const onClickHome = (e) =>{
        e.preventDefault();
        navigate("/user");
    }

    const onClickPosts = (e) =>{
        e.preventDefault();
        navigate("/posts");
    }

    //Fav  Posts
    useEffect(() => {
        services.getFavorites(token)
            .then(res => {
                const favoritesAux = res.favorites;
                
                const favoritePost = favoritesAux.map(async(Idfavorite) => {
                    const response = await services.getOnePost(token, Idfavorite);
                    return response;
                });
                Promise.all(favoritePost).then(res => {
                    const filterFavorites = res.filter(
                        (fav) => JSON.stringify(fav) !== "{}"
                    );
                    setFavorites(filterFavorites);
                });
            })
            .catch(err => {
                console.log(err);
            });
    }, [token]);
    


    return(
        <div className="w-auto bg-indigo-100 h-screen flex flex-row flex-wrap justify-center ">
            
            <div className="w-full md:w-1/4 lg:w-1/5 h-20 md:h-screen overflow-y-hidden bg-indigo-900 shadow-lg">
                <div className="invisible md:visible p-0 md:p-5 bg-indigo-900 sticky top-0">
                    <img className="w-0 h-0 md:h-full md:w-full  border-none md:border  rounded-full border-indigo-900 shadow-lg round" src={IcoUser} alt="user profile"/>
                    <div className="pt-0 md:pt-2 border-t-none md:border-t mt-0 md:mt-5 text-center text-xs md:text-xl md:text-gray-100 font-bold">
                        {user.username}
                    </div>
                </div>
                <div className="w-full h-4/5 antialiased flex flex-row md:flex-col hover:cursor-pointer ">
                    <button onClick={onClickHome} className="hover:bg-purple-500 hover:text-gray-100 bg-indigo-900 border-gray-100 border-t-2 p-3 w-full md:text-xl text-lg text-left text-gray-300 font-semibold" ><i className="fas fa-camera-retro text-gray-100 text-2xl pr-1 pt-1 float-right"></i>Home</button>
                    <button onClick={onClickHandler} className="hover:bg-red-500 hover:text-gray-100 bg-indigo-900 border-gray-100 border-t-2 border-b-none md:border-b-2 p-3 w-full text-lg md:text-xl text-left text-gray-300 font-semibold" ><i className="fa fa-arrow-left text-gray-100 text-2xl pr-1 pt-1 float-right"></i>Log out</button>
                    <img src={IcoTilin} className="invisible md:visible pt-2 w-0 md:w-3/5 mx-auto"/>
                </div>
            </div>
            
            <div className="w-full md:w-3/4 lg:w-4/5 p-5 md:px-12 lg:24 h-full overflow-x-scroll antialiased">
                {
                    favorites.map((post) => {
                        return (
                            <TilinPost
                                id={post._id}
                                title={post.title}
                                image={post.image}
                                user={post.user.username}
                                description={post.description}
                                likes={post.likes.length}
                                like={post.likes}
                                commentsCont={post.comments.length}
                                favorite={favorites.includes(post._id)}
                                active={post.active}
                                comments = {post.comments}
                            />
                        )
                    })
                }
            </div>
        </div>
    );
};

export default FavoritesUser;