import react from 'react';
import userIco from "../images/user2.jpg";

const Comment = ({comment}) =>{
    const {description, user:{username}} = comment;
    return(
        <div className="pl-4 bg-gray-800 rounded-lg p-2  flex flex-col justify-center items-start shadow-lg mb-4">
            <div className="flex flex-row justify-center mr-2">
                <img className="rounded-full h-6 w-6 mr-2    " src={userIco} alt="user"/>
                <h3 className="text-gray-100 font-semibold text-left md:text-left ">{username}</h3>
            </div>
            <p className="text-gray-100 text-center md:text-left ">{description}</p>
        </div>
    );
}

export default Comment;