import { useNavigate } from "react-router-dom";
import React, { useEffect, useState } from 'react';
import { useUserContext } from "../../contexts/UserContext";
import services from "../../services/user.services";
import TilinPost from '../../components/PostAdmin';
import IcoUser from '../../images/user.png';
import IcoTilin from '../../images/TilinIco.png'

const Admin = (props) => {
    const {token,user} = useUserContext();
    const auth = useUserContext();
    const navigate = useNavigate();
    const [posts, setPosts] = useState([]);
    const [cont , setCont] = useState(0);


    //Redirect Pages
    const onClickHandler = (e) =>{
        e.preventDefault();

        auth.logout();
        navigate("/login");
    }
    
    const onClickFav = (e) =>{
        e.preventDefault();
        navigate("/favorites_admin");
    }
    const onClickPosts = (e) =>{
        e.preventDefault();
        navigate("/posts");
    }

    //Paginacion
    useEffect(() => {
        const getAllPosts = async () => {
            try {
                const offset = 0;
                const response = await services.getPosts(token, offset);
                setPosts(response.data);
                
        } catch (error) {
            console.error(error);
        }
    };
        getAllPosts();
    }, []);

    const onNext = async () => {
        try {
            setCont(cont+1);
            const response = await services.getPosts(token,cont);
            setPosts(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const onPreview = async () => {
        try {
            if(cont >= 0){
                setCont(cont-1);
                const response = await services.getPosts(token,cont);
                setPosts(response.data)
            }
        } catch (error) {
            console.error(error);
        }
    };
    

    //Create Post
    const [titleInput, setTitleInput] = useState("");
    const [descriptionInput, setDescriptionInput] = useState("");
    const [imageInput, setImageInput] = useState("");
    const [error, setError] = useState(true);
    const [errorMessage, setErrorMessage] = useState("");
    
    const handleChangeTitle = (e) => {
        e.preventDefault();
        setTitleInput(e.target.value);
    }

    const handleChangeDescription = (e) => {
        e.preventDefault();
        setDescriptionInput(e.target.value);
    }

    const handleChangeImage = (e) => {
        e.preventDefault();
        setImageInput(e.target.value);
    }

    const onSubmitData = (e) => {
        e.preventDefault();
    }

    const dataForm = async (e) => { //> mayor que < menor que
        if(titleInput.length >= 8 && titleInput.length <= 32 && descriptionInput.length >= 8 || imageInput.length > 1){
            console.log("entro y se publico");
            const response = await services.createPost(token,titleInput,descriptionInput,imageInput);
            setErrorMessage("Published!!")
            document.getElementById("formData").reset();    
            window.location.reload("true");
        }
        else{
            console.log("no entro,F");
            console.log("Todo gucci")
            setErrorMessage("Fill the blank spaces(must be 8 characters at least)");
        }
    }

    useEffect(() => {
        services.getPosts(token,cont);
    }, []);
    
    //Boton Cancelar
    const onClickCancel = (e) =>{
        e.preventDefault();
        document.getElementById("formData").reset();
    }

    //Fav a Post
    const [favorites, setFavorites] = useState([]);
    useEffect(() => {
        services.getFavorites(token)
            .then(res => {
                setFavorites(res.favorites)
            })
            .catch(err => {
                console.log(err);
            });
    }, []);

    return(
        <div className="w-full bg-indigo-100 h-screen flex flex-row flex-wrap justify-center ">
            
            <div className="w-full md:w-1/4 lg:w-1/5 h-20 md:h-screen overflow-y-hidden bg-indigo-900 shadow-lg">
                <div className="invisible md:visible p-0 md:p-5 bg-indigo-900 sticky top-0">
                    <img className="w-0 h-0 md:h-full md:w-full  border-none md:border  rounded-full border-indigo-900 shadow-lg round" src={IcoUser} alt="user profile"/>
                    <div className="pt-0 md:pt-2 border-t-none md:border-t mt-0 md:mt-5 text-center text-xs md:text-xl md:text-gray-100 font-bold">
                        {user.username}
                    </div>
                </div>
                <div className="w-full h-4/5 antialiased flex flex-row md:flex-col hover:cursor-pointer ">
                    <button onClick={onClickFav} className="hover:bg-yellow-500 hover:text-gray-100 bg-indigo-900 border-gray-100 border-t-2 p-3 w-full text-sm md:text-xl text-left text-gray-300 font-semibold" ><i className="fa fa-star text-gray-100 text-2xl pr-1 pt-1 float-right"></i>Favorites</button>
                    <button onClick={onClickPosts} className="hover:bg-purple-500 hover:text-gray-100 bg-indigo-900 border-gray-100 border-t-2 p-3 w-full text-sm md:text-xl text-left text-gray-300 font-semibold" ><i className="fas fa-camera-retro text-gray-100 text-2xl pr-1 pt-1 float-right"></i>My Posts</button>
                    <button onClick={onClickHandler} className="hover:bg-red-500 hover:text-gray-100 bg-indigo-900 border-gray-100 border-t-2 border-b-none md:border-b-2 p-3 w-full text-sm md:text-xl text-left text-gray-300 font-semibold" ><i className="fa fa-arrow-left text-gray-100 text-2xl pr-1 pt-1 float-right"></i>Log out</button>
                    <img src={IcoTilin} className="invisible md:visible pt-2 w-0 md:w-3/5 mx-auto"/>
                </div>
            </div>
            
            <div className="w-full md:w-3/4 lg:w-4/5 p-5 md:px-12 lg:24 h-full overflow-x-scroll antialiased">

                <form id="formData" onSubmit={onSubmitData} className="editor mx-auto w-11/12 flex flex-col text-gray-800 rounded-lg border-gray-300 p-4 shadow-lg max-w-2xl">
                    <input onChange={handleChangeImage} className="title bg-gray-100 border border-gray-300 p-2 mb-4 outline-none" spellCheck="false" placeholder="Image(URL)" type="url" />
                    <input onChange={handleChangeTitle} className="title bg-gray-100 border border-gray-300 p-2 mb-4 outline-none" spellCheck="false" placeholder="Title (32 characters maximum)" type="text"  />
                    <textarea onChange={handleChangeDescription} className="description bg-gray-100 sec p-3 h-32 border border-gray-300 outline-none" spellCheck="false" placeholder="Describe everything about this post here"  ></textarea>
                    <div className="buttons flex mt-4">
                        <div onClick={onClickCancel} className="btn border hover:bg-red-500 hover:text-gray-100 border-gray-300 p-1 px-4  border-b font-semibold cursor-pointer rounded text-gray-500 ml-auto">Cancel</div>
                        <div onClick={dataForm} className="btn border border-indigo-500 p-1 px-4 font-semibold cursor-pointer border-b text-gray-200 rounded ml-2 hover:bg-indigo-300 bg-indigo-500">Add</div>
                    </div>
                {<p className="text-red-500 text-xs italic">{errorMessage}</p>}
                </form> 
                {
                    posts.map((post) => {
                        return (
                            <TilinPost
                                id={post._id}
                                title={post.title}
                                image={post.image}
                                user={post.user.username}
                                description={post.description}
                                likes={post.likes.length}
                                like={post.likes}
                                commentsCont={post.comments.length}
                                favorite={favorites.includes(post._id)}
                                active={post.active}
                                comments = {post.comments}
                                likeOn = "true"
                            />
                        )
                    })
                }
                <div className="flex justify-center items-center w-full space-x-3">
                    <button onClick={onPreview} className="w-2/5 sm:w-auto px-9 py-4 mt-8 text-base font-semibold focus:ring-2 focus:ring-offset-2 focus:ring-offset-white focus:ring-gray-900 focus:outline-none transition-colors duration-200 rounded-xl block  border-b border-indigo-300 bg-indigo-200 hover:bg-indigo-300 text-indigo-900">Back</button>
                    <button onClick={onNext} className="w-2/5 sm:w-auto px-9 py-4 mt-8 text-base font-semibold focus:ring-2 focus:ring-offset-2 focus:ring-offset-white focus:ring-gray-900 focus:outline-none transition-colors duration-200 rounded-xl block  border-b border-indigo-300 bg-indigo-200 hover:bg-indigo-300 text-indigo-900">Next</button>
                </div>
            </div>
        </div>
    );
}

export default Admin;