import react from 'react';
import userIco from "../images/user2.jpg";

const Botton = (props) => {
    return (
        <button type={props.type} class="bg-gray-900 w-full py-3 rounded-xl text-white font-bold shadow-xl hover:shadow-inner focus:outline-none transition duration-500 ease-in-out  transform hover:-translate-x hover:scale-105">
            {props.text}
        </button>
    );
}

export default Botton;
