import React, { useEffect, useState } from 'react';
import { useUserContext } from "../contexts/UserContext";
import services from "../services/user.services";
import userIco from "../images/user2.jpg";
import Comments from "./Comment";

const Post = (props) => {
    const {token,user} = useUserContext();
    var estiloLike="fas fa-heart hover:text-red-600";
    var estiloFav= props.favorite ? "fas fa-star hover:text-yellow-500" : "fas fa-star text-gray-400 hover:text-yellow-500";
    const [styleLike, setStyleLike] = useState(estiloLike);
    const [band, setBand] = useState(false);
    const [styleFav, setStyleFav] = useState(estiloFav);
    const [bandFav, setBandFav] = useState(false);
    const [like, setLike] = useState(0);
    

    //Add Like Post
    const onClickLike = (e) => {
        e.preventDefault();
        if(band){
            services.likePost(token, props.id)
                .then(res => {
                    props.likes(res.data.post);
                })
                .catch(err => {
                    console.log(err);
                });  
            estiloLike = " fas fa-heart  text-gray-400 hover:text-red-600";
            setStyleLike(estiloLike);
            setLike(like-1);
            setBand(false)
        }else{
            services.likePost(token, props.id)
            .then(res => {
                props.likes(res.data.post);
            })
            .catch(err => {
                console.log(err);
            });  
            console.log("con hover");
            estiloLike = "fas fa-heart text-red-600";
            setStyleLike(estiloLike);
            setLike(like+1);
            setBand(true);
        }
    }

    useEffect(() => {
        props.like.map(like => {
            if(like.username === user.username){
                setStyleLike("fas fa-heart text-red-600");
                setBand(true);
            }
        });
    },[]);

    //Add Favorites Post
    const onClickFav = (e) => {
        e.preventDefault();
        if(bandFav){
            services.favoritePost(token, props.id)
                
            estiloFav = " fas fa-star  text-gray-400 hover:text-yellow-500";
            setStyleFav(estiloFav);
            setBandFav(false)
        }else{
            services.favoritePost(token, props.id)
            
            estiloFav = "fas fa-star text-yellow-500";
            setStyleFav(estiloFav);
            setBandFav(true);
        }
    }

    useEffect(() => {
        services.getFavorites(token)
            .then(favs => {
                const isFavorite = favs.favorites.includes(props.id);
                if(isFavorite){
                    setStyleFav("fas fa-star text-yellow-500");
                    setBandFav(true);
                }
        });
    },[]);


    //Add Comments
    const [descriptionInput, setDescriptionInput] = useState("");
    
    const handleChangeDescription = (e) => {
        e.preventDefault();
        setDescriptionInput(e.target.value);
        console.log(descriptionInput);
    }

    const onClickComment = (e) => {
        e.preventDefault();
        services.addComment(token, props.id, descriptionInput);
        setDescriptionInput("");
    }
    

    return (
      <div className="flex justify-between m-8">
        <div className=" h-4/5 w-full md:w-3/4 lg:w-4/5 flex flex-col mx-auto bg-gray-800 rounded-lg shadow-2xl">
          <img
            className="object-fill max-h-96 max-w-7xl min-w-full rounded-lg rounded-b-none "
            src={props.image}
            alt="thumbnail"
            loading="lazy"
          />
          <div className="flex justify-between -mt-4 px-4">
            <span className="inline-block ring-4 bg-red-500 ring-gray-800 rounded-full text-sm font-medium tracking-wide text-gray-100 px-3 pt-0.5">
              Tilinstagram
            </span>
          </div>
          <div className="pt-2">
            <textarea
              className="text-xl font-medium pl-4 resize-none h-6 w-11/12 overflow-hidden leading-6 tracking-wide text-gray-100 bg-gray-800"
              disabled="true"
            >
              {props.title}
            </textarea>
          </div>
          <div className="px-5 space-y-2">
            <textarea
              className="w-11/12 leading-5 resize-none h-5 overflow-y-auto text-gray-100 font-normal tracking-wide bg-gray-800"
              disabled="true"
            >
              {props.description}
            </textarea>
          </div>
          <div className="flex flex-row items-end h-full w-full pb-4 mt-4">
            <div className="w-7/12 flex border-t border-gray-700 w-full py-4">
              <div className="pl-2.5 flex items-center space-x-3 border-r border-gray-700 w-full">
                <img
                  class="object-cover w-8 h-8 border-2 border-white rounded-full"
                  src={userIco}
                  alt="profile users"
                  loading="lazy"
                />
                <div className="">
                  <p className="text-sm font-semibold tracking-wide text-gray-200">
                    {props.user}
                  </p>
                </div>
              </div>
              <div className="flex items-center flex-shrink-0 px-2">
                <div className="flex items-center space-x-1 text-gray-400">
                  <button onClick={onClickFav} className="ch-5 w-5">
                    <i class={styleFav}></i>
                  </button>
                </div>
              </div>
              <div className="flex items-center flex-shrink-0 px-2 text-gray-400">
                <div className="flex items-center space-x-1">
                  <button onClick={onClickLike} className="group ch-5 w-5">
                    <i className={styleLike}></i>
                  </button>
                  <p className="font-medium">{props.likes + like}</p>
                </div>
              </div>
              <div className="flex items-center flex-shrink-0 px-2">
                <div className="flex items-center space-x-1 text-gray-400">
                  <button className="ch-5 w-5">
                    <i className="fas fa-comment hover:text-blue-300 "></i>
                  </button>
                  <p className="font-medium">{props.commentsCont}</p>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div class="rounded-b-lg">
              <div
                className="flex flex-row pb-4"
                action="/"
                accept-charset="UTF-8"
                method="post"
              >
                <input type="hidden" />
                <textarea
                  onChange={handleChangeDescription}
                  value={descriptionInput}
                  className="pl-4  w-11/12 shadow-inner bg-gray-100 align-middle border-0 rounded text-lg focus:shadow-outline mx-3 my-1"
                  placeholder="Write a comment."
                  cols="2"
                  rows="1"
                  id="comment_content"
                  spellcheck="false"
                ></textarea>
                <button
                  onClick={onClickComment}
                  className="font-bold content-center py-1 pr-4 w-auto hover:text-blue-500 text-white shadow-md rounded-full "
                >
                  <i class="fas fa-paper-plane"></i>
                </button>
              </div>
              <form className="h-auto max-h-24 overflow-y-auto">
                {props.comments.map((comment) => {
                  return <Comments comment={comment} />;
                })}
              </form>
            </div>
          </div>
        </div>
      </div>
    );
};

export default Post;