import react from 'react';

const Input =({type="text", placeholder,value,onChange,name})=>{
    return(
        <div>
            <input type={type} placeholder={placeholder}  value={value} onChange={onChange} name={name}
            className="mt-1 block w-full border-none bg-gray-100 h-11 rounded-xl shadow-md hover:bg-blue-100 focus:bg-blue-100 focus:ring-0"/>
        </div>
    )
}

export default Input;