import { Navigate } from "react-router-dom";
import { useUserContext } from "../../contexts/UserContext";
import IcoTilin from '../../images/TilinIco.png';

const rolePages = {
    "admin": "/admin",
    "user": "/user"
}

const Redirect = () => {
    const {user} = useUserContext();

    if(!user) {
        return (
            <div class="h-screen w-screen  bg-gray-700 flex items-center">
            <div class="container flex flex-col md:flex-row items-center justify-center px-5 text-gray-100">
                <div class="max-w-md">
                    <div class="text-5xl font-dark font-bold">Redireccionando...</div>
                    <p
                    class="text-2xl md:text-3xl font-light leading-normal"
                    >Guauf Guauf.
                    </p>
                <p class="mb-8"></p>
                
            </div>
            <div class="max-w-lg">
                <img src={IcoTilin} class="w-full h-auto" />
            </div>
            
        </div>
        </div>
        );
    }

    return <Navigate replace to={rolePages[user.role] ?? "/"} />;
}

export default Redirect;