import React, { useEffect, useState } from 'react';
import { useUserContext } from "../contexts/UserContext";
import services from "../services/user.services";
import userIco from "../images/user2.jpg";
import Comments from './Comment';


const MyPost = (props) => {
    const {token,user} = useUserContext();
    
    const [band, setBand] = useState(false);
    
    //Add Like Post
    var estiloLike="fas fa-heart hover:text-red-600";
    const [like, setLike] = useState(0);
    const [styleLike, setStyleLike] = useState(estiloLike);
    const onClickLike = (e) => {
        e.preventDefault();
        if(band){
            services.likePost(token, props.id)
            .then(res => {
                props.likes(res.data.post);
            })
            .catch(err => {
                    console.log(err);
                });  
                estiloLike = " fas fa-heart  text-gray-400 hover:text-red-600";
                setStyleLike(estiloLike);
                setLike(like-1);
                setBand(false)
            }else{
                services.likePost(token, props.id)
                .then(res => {
                    props.likes(res.data.post);
            })
            .catch(err => {
                console.log(err);
            });  
            console.log("con hover");
            estiloLike = "fas fa-heart text-red-600";
            setStyleLike(estiloLike);
            setLike(like+1);
            setBand(true);
        }
    }

    useEffect(() => {
        props.like.map(like => {
            if(like.username === user.username){
                setStyleLike("fas fa-heart text-red-600");
                setBand(true);
            }
        });
    },[]);
    
    //Add Favorites Post
    var estiloFav= props.favorite ? "fas fa-star hover:text-yellow-500" : "fas fa-star text-gray-400 hover:text-yellow-500";
    const [bandFav, setBandFav] = useState(false);
    const [styleFav, setStyleFav] = useState(estiloFav);

    const onClickFav = (e) => {
        e.preventDefault();
        if(bandFav){
            services.favoritePost(token, props.id)
            .then(res => {
                props.favs(res.data.post);
            })
            .catch(err => {
                console.log(err);
            });
            
            estiloFav = " fas fa-star  text-gray-400 hover:text-yellow-500";
            setStyleFav(estiloFav);
            setBandFav(false)
        }else{
            services.favoritePost(token, props.id)
            .then(res => {
                props.favs(res.data.post);
            })
            .catch(err => {
                console.log(err);
            });
            
            estiloFav = "fas fa-star text-yellow-500";
            setStyleFav(estiloFav);
            setBandFav(true);
        }
    }

    useEffect(() => {
        services.getFavorites(token)
            .then(favs => {
                const isFavorite = favs.favorites.includes(props.id);
                if(isFavorite){
                    setStyleFav("fas fa-star text-yellow-500");
                    setBandFav(true);
                }
        });
    },[]);
    
    //Toggle active Post
    var estiloToggle= "fas fa-eye text-green-400 hover:text-red-400";
    const [toggle, setToggle] = useState(true);
    const [styleToggle, setStyleToggle] = useState(estiloToggle);

    const onClickToggle = (e) => {
        e.preventDefault();
        if(toggle){
            services.togglePost(token, props.id)
            .then(res => {
                props.toggle(res.data.post);
            })
            .catch(err => {
                console.log(err);
            });

            estiloToggle= "fas fa-eye-slash text-red-400";
            setStyleToggle(estiloToggle);
            setToggle(false);
        }else{
            services.togglePost(token, props.id)
            .then(res => {
                props.toggle(res.data.post);
            })
            .catch(err => {
                console.log(err);
            });
            
            estiloToggle= "fas fa-eye text-green-400 hover:text-red-400";
            setStyleToggle(estiloToggle);
            
            setToggle(true);
        }   
    }

    useEffect(() => {
        if(props.active){
            setStyleToggle("fas fa-eye text-green-400 hover:text-red-400");
            setToggle(true);
        }
        else{
            setStyleToggle("fas fa-eye-slash text-red-400");
            setToggle(false);
        }
    },[]);
    
    //Edit Post
    var estiloEdit="fas fa-edit hover:text-gray-50";
    var estiloTitle="py-2 px-4";
    var estiloDescription ="px-4 space-y-2";
    
    const [edit, setEdit] = useState(false);
    const [styleEdit, setStyleEdit] = useState(estiloEdit);
    const [styleTitle, setStyleTitle] = useState(estiloTitle);
    const [styleDescription, setStyleDescription] = useState(estiloDescription);
    const [titleInput, setTitleInput] = useState("");
    const [descriptionInput, setDescriptionInput] = useState("");
    const [enabledInput, setEnbledInput] = useState(true);
    
    const handleChangeTitle = (e) => {
        e.preventDefault();
        setTitleInput(e.target.value);
        console.log(titleInput);
    }

    const handleChangeDescription = (e) => {
        e.preventDefault();
        setDescriptionInput(e.target.value);
        console.log(descriptionInput);
    }

    const onClickEdit= (e) => {
        e.preventDefault();
        if(edit){
            estiloTitle="py-2 px-4";
            estiloDescription="px-4 space-y-2"
            estiloEdit="fas fa-edit hover:text-gray-50";
            
            if(titleInput === "" && descriptionInput === ""){
                services.updatePost(token, props.id, props.title, props.description,props.image)
            }
            else if(titleInput === ""){
                services.updatePost(token, props.id, props.title, descriptionInput,props.image)
            }
            else if(descriptionInput === ""){
                services.updatePost(token, props.id, titleInput, props.description,props.image)
            }

            setStyleTitle(estiloTitle);
            setStyleDescription(estiloDescription);
            setStyleEdit(estiloEdit);
            setEdit(false);
            setEnbledInput(true);
        }else{
            estiloTitle="py-2 px-4 border-2 border-blue-400 rounded-lg";
            estiloDescription="px-4 space-y-2 border-2 border-blue-600 rounded-lg";
            estiloEdit="fas fa-check-square hover:text-gray-50"; 
            setStyleTitle(estiloTitle);
            setStyleDescription(estiloDescription);
            setStyleEdit(estiloEdit);
            setEdit(true);
            setEnbledInput(false);
        }
    }

    //Add Comments
    const [commentInput, setCommentInput] = useState("");


    const handleChangeComment = (e) => {
        e.preventDefault();
        setCommentInput(e.target.value);
    }

    const onClickComment = (e) => {
        e.preventDefault();
        services.addComment(token, props.id, commentInput);
        setCommentInput("");
    }


    return (
        <div className="flex justify-between m-8">
            <div className=" h-4/5 w-4/5 flex flex-col mx-auto bg-gray-800 rounded-lg shadow-2xl">
                <div className="flex flex-row items-end h-full w-full px-4 my-3 content-center">
                    <div className="flex items-center flex-shrink-0 px-2">
                            <div className="flex items-center space-x-1 text-gray-400">
                                <button onClick={onClickEdit} className="ch-5 w-5"><i className={styleEdit}></i></button>
                            </div>
                    </div>
                    <div className="flex items-center flex-shrink-0 px-2">
                            <div className="flex items-center space-x-1 text-gray-400">
                                <button onClick={onClickToggle} className="group ch-5 w-5"><i className={styleToggle}></i></button>
                            </div>
                    </div>
                </div>
                <img
                    className="object-fill max-h-96 max-w-7xl rounded-lg rounded-b-none"
                    src={props.image}
                    alt="thumbnail"
                    loading="lazy"
                />
                <div className="flex justify-between -mt-4 px-4">
                    <span className="inline-block ring-4 bg-red-500 ring-gray-800 rounded-full text-sm font-medium tracking-wide text-gray-100 px-3 pt-0.5">
                        Tilinstagram
                    </span>
                </div>
                <div className={styleTitle}>
                    <textarea onChange={handleChangeTitle} className="text-xl font-medium resize-none h-6 w-11/12 overflow-hidden leading-6 tracking-wide text-gray-100 bg-gray-800" disabled={enabledInput}>
                        {props.title}
                    </textarea>
                </div>  
                <div className={styleDescription}>
                    <textarea onChange={handleChangeDescription} className="w-11/12 leading-5 resize-none overflow-y-auto text-gray-100 font-normal tracking-wide bg-gray-800" disabled={enabledInput}>
                        {props.description}
                    </textarea>
                </div>
                <div className="flex flex-row items-end h-full w-full px-4 mt-4">
                    <div className="flex border-t border-gray-700 w-full py-4">
                        <div className="flex items-center space-x-3 border-r border-gray-700 w-full">
                            <img
                                class="object-cover w-8 h-8 border-2 border-white rounded-full"
                                src={userIco}
                                alt="profile users"
                                loading="lazy"
                            />
                            <div className="">
                                <p className="text-sm font-semibold tracking-wide text-gray-200">
                                    {props.user}
                                </p>
                            </div>
                        </div>
                        <div className="flex items-center flex-shrink-0 px-2">
                            <div className="flex items-center space-x-1 text-gray-400">
                                <button onClick={onClickFav} className="ch-5 w-5"><i class={styleFav}></i></button>
                            </div>
                        </div>
                        <div className="flex items-center flex-shrink-0 px-2 text-gray-400">
                            <div className="flex items-center space-x-1">
                                <button onClick={onClickLike} className="group ch-5 w-5"><i className={styleLike} ></i></button>
                                <p className="font-medium">{props.likes + like}</p>
                            </div>
                        </div>
                        <div className="flex items-center flex-shrink-0 px-2">
                            <div className="flex items-center space-x-1 text-gray-400">
                                <button className="ch-5 w-5"><i className="fas fa-comment hover:text-blue-300 "></i></button>
                                <p className="font-medium">{props.commentsCont}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div>
                    <div class="rounded-b-lg">    
                        <div className="flex flex-row pb-4" action="/" accept-charset="UTF-8" method="post"><input type="hidden"/>
                                <textarea onChange={handleChangeComment} value={commentInput} className="pl-4  w-11/12 shadow-inner bg-gray-100 align-middle border-0 rounded text-lg focus:shadow-outline mx-3 my-1" placeholder="Write a comment." cols="2" rows="1" id="comment_content" spellcheck="false"></textarea>
                                <button  onClick={onClickComment} className="font-bold content-center py-1 pr-4 w-auto hover:text-blue-500 text-white shadow-md rounded-full "><i class="fas fa-paper-plane"></i></button>
                        </div>
                            <form className="h-auto max-h-24 overflow-y-auto">
                                {
                                    props.comments.map(comment => {
                                        return <Comments comment={comment}/>
                                    })
                                }
                            </form>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default MyPost;