import { useState, useEffect } from "react";
import { useUserContext } from "../../contexts/UserContext";
import { useNavigate } from "react-router-dom";
import Input from "../../components/Input";
import Botton from "../../components/Botton";
import IcoTilin from '../../images/TilinIco.png'

const Login = () => {
    // Recibimos la función login y el token guardadas en UserContext
    const navigate = useNavigate();

    const { login, token } = useUserContext();

    const [usernameInput, setUsernameInput] = useState("");
    const [passwordInput, setPasswordInput] = useState("");
    const [error, setError] = useState(false);

    // Se va guardando cada caracter que ingresamos al input
    const onChangeHandler = (e, save) => {
        console.log(e.target.value);
        save(e.target.value);
    }

    const onSubmitHandler = async (e) => {
        e.preventDefault();

        const logged = await login(usernameInput, passwordInput);
        setError(!logged);

        setUsernameInput("");
        setPasswordInput("");
    }

    useEffect(()=>{
        console.log(token);
        if(token){
            navigate("/redirect");
        }
    }, [token, navigate]);

    return (
        <div className=" relative font-sans" >
            <div className="fixed content-center md:relative min-h-screen flex flex-col md:justify-center items-center bg-cover bg-gray-700">
                <div className="relative md:max-w-md w-full m-28">
                    <div className="card bg-gray-900 shadow-2xl  w-full h-full rounded-3xl absolute  transform -rotate-6 "></div>
                    <div className="card bg-purple-500 shadow-2xl  w-full h-full rounded-3xl absolute  transform rotate-6 "></div>
                    <div className="relative w-full rounded-3xl  px-10 py-4 bg-gray-100 shadow-md ">
                        <img src={IcoTilin} className="w-3/5 mx-auto"/>
                        <form onSubmit={onSubmitHandler} className="mt-10">                                           
                            <div>
                                <Input type="text" placeholder="Username" name="email or user" onChange={e=>onChangeHandler(e,setUsernameInput)} required/>
                            </div>
                            <div className="mt-7">                
                                <Input type="Password" placeholder="Password" name="password" onChange={e=>onChangeHandler(e,setPasswordInput)} required/>
                            </div>
                            
                            {error && <p className="text-red-500 text-xs italic">Usuario o contraseña incorrectos</p>}
                            
                            <div className="mt-7">
                                <Botton text="Login" type="submit"/>
                            </div>
                
                            <div className="flex mt-7 items-center text-center">
                                <hr className="border-gray-300 border-1 w-full rounded-md"></hr>
                                <label className="block font-medium text-sm text-gray-600 w-full">
                                    Tilin's Dev 
                                </label>
                                <hr className="border-gray-300 border-1 w-full rounded-md"></hr>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Login